.most-viewed-title{
    flex: 1;
    padding: 0 0 0 10px;
    h2{
        font-size: 14px;
        font-weight: 600;
        line-height: 18px;
        margin: 0;
        display: inline-block;
        -webkit-line-clamp: 3;
        -webkit-box-orient: vertical;
        display: -webkit-box;
        overflow: hidden;
        
    }
    h2 a{
        font-size: 15px !important;
        color: #1c1c1c;
        // line-height: 28px;
        margin-top: 0; 
         border: 0;
         padding: 0;
    }
}