.admin{
    height: 100%;
    background-color:rgb(244, 244, 244);
    &__user{
        display: flex;
        color: white;
        font-size: 20px;
        
    }
    &__link{
        display: flex;
        text-decoration: none;
        color: white;
        font-size: 20px;
        margin: 15px 0;
        >i{
            margin: 0 20px ;
        }
        &:hover{
            color: red;
        }
    }

    &__children{
        background-color: rgb(244, 244, 244) ;
    }
}

.icons{
    font-size: 22px;
}
.btn-logout{
    &:hover{
        color: red;
        cursor: pointer;
    }
}

.news-admin{
    background-color: rgb(244, 244, 244) ;
    height: 100vh;
    
}
.publicity-admin{
    background-color: rgb(244, 244, 244) ;
    height: 100vh;
}


.sidebar-admin{
    width: 100%;
    background-color: #2f4050;
    
    height: 100vh;
}

.admin-header{
    width: 100%;
    height: 100%;
    background-color: #2f4050;

}

